.circleinfo {
    transition: all 0.5s ease;
    width: 250px;
    height: 250px;
    border-radius: 50%;
    background-color: #002BFF;
    margin-left: 10px;
    margin-top: 1000px;
    position: absolute;
    box-shadow: 0px 15px 50px lightblue;
    border: solid lightblue 3px;
    z-index: 2;
}
.circleinfo img {
    width: 100%;
    border-radius: 50%;

}
.circleinfo.show {
    margin-left: 10px;
    margin-top: -10px;
}

/* glow */
.circleinfo:hover::before {
  content: "";
  background: linear-gradient(
    45deg,
    #00FFD5, #002BFF,lightblue
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 600%;
  z-index: -1;
  width: calc(100% + 4px);
  height:  calc(100% + 4px);
  filter: blur(8px);
  animation: glowing 20s linear infinite;
  transition: opacity .3s ease-in-out;
  border-radius: 50%;
  opacity: 1;
}
@keyframes glowing {
    0% {background-position: 0 0;}
    50% {background-position: 400% 0;}
    100% {background-position: 0 0;}
  }
  .circleinfo:hover {
    opacity: 1;
    transform: scale(1.2) rotate(360deg);
  }

.infobox
